import './App.css';
import oxidation_resistance_poster from './images/oxidation_resistance_poster.png'
import oxidation_resistance_poster_thumbnail from './images/oxidation_resistance_poster_thumbnail_uniform.png'
import protocell_self_assembly_poster from './images/protocell_self_assembly_poster.png'
import protocell_self_assembly_poster_thumbnail from './images/protocell_self_assembly_poster_thumbnail_uniform.png'
import rgc_neuro_poster from './images/rgc_neuro_poster.png'
import rgc_neuro_poster_thumbnail from './images/rgc_neuro_poster_thumbnail_uniform.png'
import variant_caller_poster from './images/variant_caller_poster.png'
import variant_caller_poster_thumbnail from './images/variant_caller_poster_thumbnail_uniform.png'

import SideNav, { Toggle, Nav, NavItem, NavIcon, NavText } from '@trendmicro/react-sidenav';
import StyledSideNav from "./StyledNav";
import styled from 'styled-components';
import React, { Component } from 'react';

import 'photoswipe/dist/photoswipe.css'
import 'photoswipe/dist/default-skin/default-skin.css'

import { Gallery, Item } from 'react-photoswipe-gallery'


// Define main component here
const Main = styled.main`
    position: relative;
    overflow: hidden;
    transition: all .15s;
    padding: 20px 20px;
    margin-left: ${props => (props.expanded ? 240 : 64)}px;
`;


function Print(props) {
  return <text>name = {props.name}</text>;
}


function Posters(){
  return(
    <div>
      <h1>Posters</h1>

      <Gallery>
        <Item
            original={oxidation_resistance_poster}
            thumbnail={oxidation_resistance_poster_thumbnail}
            width="1024"
            height="768"
        >
          {({ ref, open }) => (
              <img ref={ref} onClick={open} src={oxidation_resistance_poster_thumbnail} />
          )}
        </Item>
        <Item
            original={protocell_self_assembly_poster}
            thumbnail={protocell_self_assembly_poster_thumbnail}
            width="1024"
            height="768"
        >
          {({ ref, open }) => (
              <img ref={ref} onClick={open} src={protocell_self_assembly_poster_thumbnail} />
          )}
        </Item>
        <Item
            original={rgc_neuro_poster}
            thumbnail={rgc_neuro_poster_thumbnail}
            width="1024"
            height="768"
        >
          {({ ref, open }) => (
              <img ref={ref} onClick={open} src={rgc_neuro_poster_thumbnail} />
          )}
        </Item>
        <Item
            original={variant_caller_poster}
            thumbnail={variant_caller_poster_thumbnail}
            width="1024"
            height="768"
        >
          {({ ref, open }) => (
              <img ref={ref} onClick={open} src={variant_caller_poster_thumbnail} />
          )}
        </Item>
      </Gallery>
    </div>
  );
}


function Content(props){
  if (props.name === 'home') {
    return(
      <h1>Home</h1>
    );
  } else if (props.name === 'publications'){
    return(
      <h1>Publications</h1>
    );
  } else if (props.name === 'projects'){
    return(
      <h1>Projects</h1>
    );
  } else if (props.name === 'posters'){
    return(
      <Posters/>
    )
  } else {
    return(
      <div>Error unknown page identifier {JSON.stringify(props.name)}</div>
    );
  }
}


class App extends Component {
  state = {
    selected: 'home',
    expanded: false
  };

  // onSelect = (selected) => {
  //   this.setState({ selected: selected });
  // };
  // onToggle = (expanded) => {
  //   this.setState({ expanded: expanded });
  // };

  render() {
    return (
      <div>
        <StyledSideNav
            onSelect = {(selected) => {
              this.setState({ selected: selected });
            }}
            onToggle = {(expanded) => {
              this.setState({ expanded: expanded });
            }}
          >
          <StyledSideNav.Toggle/>
          <StyledSideNav.Nav defaultSelected="home">
            <NavItem eventKey="home">
              <NavIcon>
                <i className="fa fa-fw fa-home" style={{fontSize: '1.75em'}}/>
              </NavIcon>
              <NavText>
                Home
              </NavText>
            </NavItem>
            <NavItem eventKey="publications">
              <NavIcon>
                <i className="fa fa-fw fa-publications" style={{fontSize: '1.75em'}}/>
              </NavIcon>
              <NavText>
                Publications
              </NavText>
            </NavItem>
            <NavItem eventKey="projects">
              <NavIcon>
                <i className="fa fa-fw fa-projects" style={{fontSize: '1.75em'}}/>
              </NavIcon>
              <NavText>
                Projects
              </NavText>
            </NavItem>
            <NavItem eventKey="posters">
              <NavIcon>
                <i className="fa fa-fw fa-posters" style={{fontSize: '1.75em'}}/>
              </NavIcon>
              <NavText>
                Posters
              </NavText>
            </NavItem>
          </StyledSideNav.Nav>
        </StyledSideNav>
          <Main expanded={this.state.expanded}>
            <Content name={this.state.selected} />
          </Main>
      </div>
    );
  }
}

export default App;
